@import url(https://fonts.googleapis.com/css?family=Merienda|Roboto+Mono:300,400&display=swap&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=Quicksand:300&display=swap);
*{box-sizing:border-box}html{scroll-behavior:smooth}body{margin:0;padding:0}

.progressbar{width:100vw;background-color:rgba(82,109,122,0.9);box-shadow:0 5px 15px -10px #333;position:fixed;top:0;height:50px;z-index:9}.progressbar ul{display:flex;justify-content:space-evenly;align-items:center;list-style-type:none;margin:0 auto;padding:0;height:100%;max-width:1440px}.progressbar ul li{font-size:1.4rem;cursor:pointer}.progressbar ul li a{text-decoration:none;color:inherit;display:block}.progressbar ul li:hover{color:#f5f5f5}.active{color:#f5f5f5;-webkit-transition:100ms;transition:100ms}@media (max-width: 1024px){.progressbar{width:50px;right:0;background:none;box-shadow:none;background-image:url("data:image/png;base64,");background-repeat:no-repeat;background-position:center;background-size:80%;cursor:pointer}.progressbar ul{position:fixed;flex-direction:column;align-items:flex-start;justify-content:flex-start;right:0;left:0;height:auto;padding-top:50px;-webkit-transform:translateY(-100vw);transform:translateY(-100vw)}.progressbar ul li{width:100vw;background-color:rgba(82,109,122,0.9)}.progressbar ul li:not(:last-of-type){border-bottom:1px solid #999}.progressbar ul li>a{padding:4px 50px}.progressbar:hover>ul,.progressbar:focus>ul,.progressbar ul:focus-within{-webkit-transform:translateY(0);transform:translateY(0);-webkit-transition:400ms;transition:400ms}}

.box{margin:10px;display:flex;flex-direction:row-reverse;align-items:center;justify-content:center;z-index:1}.box__img{background-color:rgba(204,204,204,0.4);border:2px solid #dbdbdb;box-shadow:0 15px 20px -10px rgba(77,77,77,0.7);border-radius:10px;font-size:1rem;overflow:hidden;min-width:150px;max-width:300px}.box__img img{display:block;width:100%;height:auto;object-fit:cover}.box__text{background-color:rgba(77,77,77,0.4);box-shadow:0 15px 20px -10px rgba(77,77,77,0.7);max-width:880px;border:2px solid #dbdbdb;border-radius:10px;padding:10px 20px;font-size:1rem;align-self:stretch;display:flex;justify-content:center;align-items:center}.box__text h3{font-weight:300;margin:0 0 10px 0}.box__line{align-self:center;border-top:2px solid #dbdbdb;flex-grow:1;min-width:20px}.box__dot{min-width:20px;min-height:20px;border-radius:50%;border:2px solid #dbdbdb;background-color:#333}.box__dot:hover{background-color:#4d4d4d}.box__dot:hover,.box__dot:hover ~ div{border-color:#fff;cursor:pointer}.right{align-self:flex-start;margin-left:calc(50% - (20px / 2 ) + 5px);flex-direction:row}.right .box__img{margin-left:5px}.left{align-self:flex-end;margin-right:calc(50% - (20px / 2 ) - 5px)}.left .box__img{margin-right:5px}@media (max-width: 1024px){.box__line{min-width:20px;max-width:20px}.box__img{margin-right:0}.right,.left{align-self:flex-start;margin-left:calc(10% - (20px / 2 ) + (2px));margin-right:auto;flex-direction:row}.right .box__img,.left .box__img{margin:0 0 0 5px}}@media (max-width: 340px){.box__img{min-width:100px}}@media only screen and (-webkit-min-device-pixel-ratio: 1.3), only screen and (-webkit-min-device-pixel-ratio: 1.25), only screen and (min-resolution: 120dpi){.box{max-width:calc(100vw - 10% + (20px / 2 ))}.box__line{max-width:100px}.right,.left{align-self:flex-start;margin-left:calc(10% - (20px / 2 ) + (2px));margin-right:auto;flex-direction:row}.right .box__img,.left .box__img{margin:0 0 0 5px}}

.content{position:relative;padding:0 5px;background-color:#666;background:-webkit-gradient(linear, left bottom, left top, from(#d98c8c), color-stop(10%, #d9ba8c), color-stop(20%, #c9d98c), color-stop(30%, #9cd98c), color-stop(40%, #8cd9ab), color-stop(50%, #8cd9d9), color-stop(60%, #8cabd9), color-stop(70%, #9c8cd9), color-stop(80%, #c98cd9), color-stop(90%, #d98cba), to(#d98c8c));background:linear-gradient(0deg, #d98c8c 0%, #d9ba8c 10%, #c9d98c 20%, #9cd98c 30%, #8cd9ab 40%, #8cd9d9 50%, #8cabd9 60%, #9c8cd9 70%, #c98cd9 80%, #d98cba 90%, #d98c8c 100%)}.content section{max-width:100vw;color:#f5f5f5;padding:60px 0;display:flex;flex-direction:column;justify-content:center}.content .intro{justify-content:flex-start}.content .intro h1{font-family:'Merienda', cursive;font-size:6rem;color:#e6a8a8;text-shadow:5px -5px 0 #b81414;margin:0 0 0 10%}.content .intro h2{font-family:'Roboto Mono', monospace;font-weight:300;font-size:2rem;color:#f1bcbc;text-shadow:1px -2px 0 #8a0f0f;margin:0 0 0 15%}.content .outro{margin-top:100px}.content .outro__episodes{max-width:100vw;display:flex;flex-wrap:wrap;justify-content:center;text-align:center}.content .outro__episode{border:1px solid #f5e7a3;background-color:rgba(235,207,71,0.4);width:100px;height:100px}.content .outro__episode-wrap{margin:5px}.content .outro__episode img{width:100%;height:auto;object-fit:cover;-webkit-filter:grayscale(20%) brightness(80%);filter:grayscale(20%) brightness(80%);cursor:pointer}.content .outro__episode img:hover{-webkit-filter:brightness(120%);filter:brightness(120%)}.content .line{top:650px;bottom:650px;width:2px;background-color:#dbdbdb}.content .line,.content .line__top,.content .line__bottom{position:absolute;max-height:100%;margin-left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.content .line__top,.content .line__bottom{width:20px;height:20px;border-radius:50%;border:2px solid #dbdbdb;background-color:#333}.content .line__top{top:calc(-20px / 2)}.content .line__bottom{bottom:calc(-20px / 2)}.content__info{border:1px solid #f5e7a3;background-color:rgba(235,207,71,0.4);box-shadow:0 10px 20px -10px rgba(77,77,77,0.7);max-width:1000px;align-self:center;margin:30px 15vw;padding:20px;font-size:1.6rem}.content__info h3{margin:0;font-weight:300}@media (max-width: 1024px){.content .line,.content .line__top,.content .line__bottom{margin-left:calc(10% + (2px / 2))}}@media only screen and (-webkit-min-device-pixel-ratio: 1.3), only screen and (-webkit-min-device-pixel-ratio: 1.25), only screen and (min-resolution: 120dpi){.content .intro h1{font-size:4rem}.content .line{margin-left:10%}.content .line__top,.content .line__bottom{margin-left:calc(10% + (2px / 2))}}

.footer{height:100px;background-color:#4d4d4d;border:1px solid #333;display:flex;flex-direction:column;justify-content:flex-end;align-items:center}.footer__copyrights{margin:10px;color:#b3b3b3}

.loader{background-color:#666;color:#f5f5f5;position:absolute;top:0;bottom:0;left:0;right:0;display:flex;justify-content:center;align-items:center}.loader__err{position:absolute;top:50px;margin-left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);color:#f66}.loader__content{border-radius:50%;width:100px;height:100px;position:absolute;border-right:4px solid transparent;border-top:4px solid;-webkit-animation:loader linear infinite;animation:loader linear infinite}.loader .red{border-top-color:rgba(255,82,197,0.6);-webkit-animation-duration:1s;animation-duration:1s}.loader .green{border-top-color:rgba(82,255,82,0.6);-webkit-animation-duration:1200ms;animation-duration:1200ms}.loader .blue{border-top-color:rgba(82,139,255,0.6);-webkit-animation-duration:1400ms;animation-duration:1400ms}@-webkit-keyframes loader{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}90%{-webkit-transform:rotate(340deg);transform:rotate(340deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loader{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}90%{-webkit-transform:rotate(340deg);transform:rotate(340deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.app{font-family:'Quicksand', sans-serif;max-width:100vw}

